.story{
    position: relative;
    background-position: center center;
    background-size: cover;
    background-repeat: no-repeat;
    width: 120px;
    height: 40vh;
    box-shadow: 0p 3px 17px -7px black;
    border-radius: 10px;
    margin-right: 10px;
    transition: transform 100ms ease-in;
    cursor: pointer;
}
.story:hover{
    transform: scale(1.07);
}
.story_avatar{
    margin: 10px;
    border: 5px solid #2e81f4;
         
}
.story h4{
    position: absolute;
    bottom: 20px;
    left: 20px;
    color: white;

}
