.post{
    width: 100%;
    margin-top: 15px;
    border-radius: 15px;
    background-color: white;
    box-shadow: 0px 5px 7px -7px black;
}
.post_top{
    display: flex;
    position: relative;
    align-items: center;
    padding: 15px;
}
.post_avatar{
    margin-right: 10px;
}
.post_topInfo h3{
    font-size: medium;
}
.post_image img{
    width: 100%;
    aspect-ratio: 3/3;
    object-fit: contain;
}
.post_topInfo p{
    font-size: small;
    color: gray;
}
.post_bottom{
    margin-top: 10px;
    margin-bottom: 10px;
    padding: 15px 25px;
}

.post_options{
    padding-top: 10px;
    border-top: 1px solid lightgrey;
    display: flex;
    justify-content: space-evenly;
    font-size: medium;
    color: gray;
    cursor: pointer;
    padding: 15px;
}
.post_option{
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 5px;
    flex: 1;
}
.post_option p{
    margin-left: 10p;
}
.post_option:hover{
    background-color: aliceblue;
    border-radius: 10px;
}